import React from "react";
import ReactApexChart from "react-apexcharts";
import "./Dashboard.css";
export default class ApexChart extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      series: [44, 55, 41, 17],

      options: {
        dataLabels: {
          enabled: false,
        },
        legend: {
          position: "bottom",
        },
        plotOptions: {
          pie: {
            donut: {
              size: 90,
              labels: {
                show: true,
                total: {
                  show: true,
                  label: "",
                  formatter: () => "Downloads",
                },
              },
            },
          },
        },
        chart: {
          type: "donut",
        },
        labels: ["Mac", "Window", "Ios", "Android"],
        responsive: [
          {
            breakpoint: 480,
            options: {
              chart: {
                width: 180,
              },
              legend: {
                position: "bottom",
              },
            },
          },
          {
            breakpoint: 1348,
            options: {
              chart: {
                width: 250,
              },
            },
          },
          {
            breakpoint: 1153,
            options: {
              chart: {
                width: 200,
              },
            },
          },
        ],
      },
    };
  }

  render() {
    return (
      <div id="chart">
        <ReactApexChart
          options={this.state.options}
          series={this.state.series}
          width={330}
          height={300}
          type="donut"
        />
      </div>
    );
  }
}
