import React, { Fragment, useState } from "react";
import { Card, CardBody, Col, Container, Media, Row , Button, Modal, ModalHeader, ModalBody, ModalFooter } from "reactstrap";
import DataTable from "react-data-table-component";
import { Link, useNavigate } from "react-router-dom";
import "../Dashboard/Dashboard.css";
import { useEffect } from "react";
import axios from "axios";
import APIURL from "../../Utils/api-url";
import LoaderSpinner from "../Loader/Loader";
import { successMessage } from "../../Utils/ToasterMessage";
const corsAnywhere = 'https://cors-anywhere.herokuapp.com/';

const AdminTable = () => {
  const [Sidebar, setSidebar] = useState(false);
  const OnClickHandel = () => {
    setSidebar(!Sidebar);
  };
  const [search, setSearch] = useState([]);
  const [members, setmembers] = useState([]);
  const [filterData, setfilterdata] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [deleteID, setDeleteID] = useState();
  const [reloadPage, setReloadPage] = useState(false);
  const [modal, setModal] = useState(false);

  const toggle = () => setModal(!modal);


  const navigate = useNavigate("");



  useEffect(() => {
    setIsLoading(true);
    const fetchData = async () => {
      const results = await axios.get(
        `${APIURL.MemberList}`
      );
      // console.log(results.data);
      setIsLoading(false);
      setmembers(results.data);
      setfilterdata(results.data);
    };
    fetchData();
  }, [reloadPage]);

  // const getMember = async () => {
  //   try {
  //     const response = await axios.get(`${corsAnywhere}${APIURL.MemberList}`, {
  //       headers: {
  //         crossorigin:true,
  //         "Access-Control-Allow-Origin": "*",
  //       },
  //     });
  //     console.log(response.data);
  //     setmembers(response.data);
  //     setfilterdata(response.data);
  //   } catch (error) {
  //     console.log(error);
  //   }
  // };


  const Remove = async () => {
    //debugger
    axios
      .delete(
        `${APIURL.deletemember}/${deleteID}`,
      )
      .then((res) => {
        // console.log(res.data);
        setReloadPage(!reloadPage);
        successMessage("Admin remove successfully")
      })
      .catch((err) => {
        console.log(err.data);

      });
  };
  const columns = [
    {
      name: "ID",
      selector: (row) => row.id,
      sortable: true,
    },
    {
      name: "Username",
      selector: (row) => row.display_name,
      sortable: true,
    },
    {
      name: "Email ID",
      selector: (row) => row.user_email,
      sortable: true,
    },

    {
      name: "Registered",
      selector: (row) => row.registered,
      sortable: true,
    },
    {
      name: "Status",
      selector: (row) => {
        return row?.status == "complete" ? <input disabled style={{border:"1px solid lightgreen" ,textAlign:"center" , width:"70px" , borderRadius:"8px" , backgroundColor:"lightgreen" , color:"black"}} value={row?.status}/>
        :<input disabled style={{border:"1px solid red" , width:"70px" , borderRadius:"8px" ,textAlign:"center" , backgroundColor:"red" , color:"floralwhite"}} value={row?.status}/>

      },
      // sortable: true,
    },
    {
      name: "Action",
      selector: (row) => (
        <div className="d-flex align-item-center">
          <p>
          <span
              type="button"
              data-bs-toggle="modal"
              data-bs-target="#deletemember"
              onClick={() => {
                setDeleteID(row.id);
              }}
            >
          <i className="fa fa-trash"
            title="Delete"
              style={{
                color: "Red",
                marginLeft: "15px",
                fontSize: "18px",
                flexGrow: "0",
                cursor: "pointer",
              }}
              onClick={toggle}
             ></i>
              </span>
          </p>
        </div>
      ),
    },
  
  ];

  useEffect(() => {
    console.log(search);
    if (search) {
        let filterddata = members.filter((mapping) => {
            return String(mapping?.username).toLowerCase().includes(search) ||
            String(mapping?.email).toLowerCase().includes(search) ||
                String(mapping?.id).toLowerCase().includes(search) 
        })
        setfilterdata(filterddata)
    } else {
      setfilterdata(members)
    }
}, [search])


  // useEffect(() => {
  //   getMember();
  // }, []);

  

  return (
    <Fragment>
      <Container fluid={true} className="" style={{width:"90%"}}>
        <div className="margin_left">
          <Row className="m-1">
          <Card className="shadow-sm corner" style={{ width: "100%" }}>
              <CardBody>
            <DataTable
              columns={columns}
              data={filterData}
              progressPending={isLoading}
              progressComponent={<LoaderSpinner />}
              pagination
              paginationPerPage={50}
              paginationRowsPerPageOptions={[50, 100, 200, 500]}
              paginationComponentOptions={{
                rowsPerPageText: "Records per page:",
                rangeSeparatorText: "out of",
              }}
              fixedHeader
              fixedHeaderScrollHeight="400px"
              // selectableRows
              // selectableRowsHighlight
              highlightOnHover
              subHeader
              subHeaderComponent={
                <input
                  type="text"
                  placeholder="Search here"
                  className="w-25 form-control"
                  value={search}
                  onChange={(e) => setSearch(e.target.value)}
                />
              }
            />
            </CardBody>
            </Card>
          </Row>
        </div>
        <Modal isOpen={modal} toggle={toggle}>
        <ModalBody className="text-center">
        Are you want to remove this member
        </ModalBody>
        <ModalFooter className="justify-content-center">
          <span
              type="button"
              data-bs-toggle="modal"
              data-bs-target="#deletemember"
              onClick={() => Remove(deleteID)}
            >
             <Button color="primary" onClick={toggle}>
            Delete
          </Button>{' '}
              </span>
          <Button color="secondary" onClick={toggle}>
            Cancel
          </Button>
        </ModalFooter>
      </Modal>
      </Container>
    </Fragment>
  );
};
export default AdminTable;
