import React, { Fragment, useState } from "react";
import { Card, CardBody, Container, Row , Button, Modal, ModalBody, ModalFooter } from "reactstrap";
import DataTable from "react-data-table-component";
import { Link, useNavigate } from "react-router-dom";
import "../Dashboard/Dashboard.css";
import "./Member.css";
import { useEffect } from "react";
import axios from "axios";
import APIURL from "../../Utils/api-url";
import LoaderSpinner from "../Loader/Loader";
import { successMessage } from "../../Utils/ToasterMessage";
// const corsAnywhere = 'https://cors-anywhere.herokuapp.com/';

const MemberTable = () => {
  const [Sidebar, setSidebar] = useState(false);
  const OnClickHandel = () => {
    setSidebar(!Sidebar);
  };
  const [search, setSearch] = useState([]);
  const [members, setmembers] = useState([]);
  const [filterData, setfilterdata] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [deleteID, setDeleteID] = useState();
  const [reloadPage, setReloadPage] = useState(false);
  const [modal, setModal] = useState(false);

  const toggle = () => setModal(!modal);

  const navigate = useNavigate("");

  const getMember = async () => {
    setIsLoading(true);
    try {
      const response = await axios.get(`${APIURL.Member}`
      );
      // console.log(response.data);
      setmembers(response.data);
      setIsLoading(false);
      setfilterdata(response.data);
    } catch (error) {
      setIsLoading(false);
      // console.log(error);
    }
  };

  const Remove = async () => {
    //debugger
    axios
      .delete(
        `${APIURL.deletemember}/${deleteID}`,
      )
      .then((res) => {
        // console.log(res.data);
        setReloadPage(!reloadPage);
        successMessage("Member removed successfully")
      })
      .catch((err) => {
        // console.log(err.data);

      });
  };

  
  const columns = [
    {
      name: "ID",
      selector: (row) => row.id,
      sortable: true,
    },
    {
      name: "Username",
      selector: (row) => row.display_name,
      sortable: true,
    },
    {
      name: "Email ID",
      selector: (row) => row.user_email,
      sortable: true,
    },

    {
      name: "Registered",
      selector: (row) => {
        let newDob = new Date(row?.registered);
        let formattedDateCal = newDob.toLocaleDateString("en-GB", {
          day: "numeric",
          month: "numeric",
          year: "numeric",
           hour: "numeric",
          minute: "numeric",
          second: "numeric",
        });
        return formattedDateCal;
      },
      // selector: (row) => row.registered,
      sortable: true,
    },
    {
      name: "Status",
      selector: (row) => {
        return row?.status == "completed" ? <input disabled style={{border:"1px solid lightgreen" ,textAlign:"center" , width:"70px" , borderRadius:"8px" , backgroundColor:"lightgreen" , color:"black"}} value={row?.status == "completed" ? "Completed" : ""}/>
        :<input disabled style={{border:"1px solid red" , width:"70px" , borderRadius:"8px" ,textAlign:"center" , backgroundColor:"red" , color:"floralwhite"}} value={row?.status == "pending" ? "Pending" : ""}/>

      },
      // sortable: true,
    },
    {
      name: "Action",
      selector: (row) => (
        <div className="d-flex align-item-center">
          <p>
          <span
              type="button"
              data-bs-toggle="modal"
              data-bs-target="#deletemember"
              onClick={() => {
                setDeleteID(row.id);
              }}
            >
          <i className="fa fa-trash"
            title="Remove"
              style={{
                color: "Red",
                marginLeft: "15px",
                fontSize: "18px",
                flexGrow: "0",
                cursor: "pointer",
              }}
              onClick={toggle}
             ></i>
              </span>
          </p>
        </div>
      ),
    },
  
  ];

  useEffect(() => {
    // console.log(search);
    if (search) {
        let filterddata = members.filter((mapping) => {
            return String(mapping?.display_name).toLowerCase().includes(String(search).toLowerCase()) ||
            String(mapping?.user_email).toLowerCase().includes(String(search).toLowerCase()) ||
                String(mapping?.id).toLowerCase().includes(String(search).toLowerCase()) 
        })
        setfilterdata(filterddata)
    } else {
      setfilterdata(members)
    }
}, [search])


  useEffect(() => {
    getMember();
  }, [reloadPage]);

  return (
    <Fragment>
      <Container fluid={true} className="" style={{width:"90%"}}>
      
        <div className="margin_left">
          <Row className="m-1">
          <Card className="shadow-sm corner" style={{ width: "100%" }}>
              <CardBody>
              <Link
          className="btn btn-primary mr-3 p-2 "
          style={{ color: "white", float: "right ",}}
          to="/add-member"
        >
          {" "}
          <i className="fa fa-plus-circle mx-2 mt-2"></i>
          Add Member
        </Link>
            <DataTable
              columns={columns}
              data={filterData}
              progressPending={isLoading}
              progressComponent={<LoaderSpinner />}
              pagination
              paginationPerPage={50}
              paginationRowsPerPageOptions={[50, 100, 200, 500]}
              paginationComponentOptions={{
                rowsPerPageText: "Records per page:",
                rangeSeparatorText: "out of",
              }}
              fixedHeader
              fixedHeaderScrollHeight="400px"
              // selectableRows
              // selectableRowsHighlight
              highlightOnHover
              subHeader
              subHeaderComponent={
                <input
                  type="text"
                  placeholder="Search here"
                  className="w-25 form-control"
                  value={search}
                  onChange={(e) => setSearch(e.target.value)}
                />
              }
            />
            </CardBody>
            </Card>
          </Row>
        </div>
        <Modal isOpen={modal} toggle={toggle}>
        <ModalBody className="text-center">
        Are you want to remove this member
        </ModalBody>
        <ModalFooter className="justify-content-center">
          <span
              type="button"
              data-bs-toggle="modal"
              data-bs-target="#deletemember"
              onClick={() => Remove(deleteID)}
            >
             <Button color="primary" onClick={toggle}>
            Delete
          </Button>{' '}
              </span>
          <Button color="secondary" onClick={toggle}>
            Cancel
          </Button>
        </ModalFooter>
      </Modal>
      </Container>
    </Fragment>
  );
};
export default MemberTable;
